<template>
  <div>
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">장바구니</span>
    </v-card-title>
    <v-tabs v-model="tab" background-color="beige" color="black" grow>
      <v-tab key="saleProduct" class="font-weight-bold body-1">
        판매상품({{ $store.state.cart.cartList.length }}) </v-tab
      ><v-tab key="soldOutProduct" class="font-weight-bold body-1">
        품절상품({{ $store.state.cart.soldOutList.length }})
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item key="saleProduct">
        <v-card flat>
          <!-- 판매상품-->
          <div>
            <v-checkbox
              class="ma-4 pa-0"
              hide-details
              label="전체 선택"
              v-model="allChecked"
              @click="checkedAll(allChecked)"
            ></v-checkbox>
            <v-card
              class="rounded-lg ma-3"
              v-for="(cart, indexOfCartList) in $store.state.cart.cartList"
              :key="indexOfCartList"
            >
              <v-list-item three-line>
                <v-list-item-content class="ma-0 my-1 pa-0">
                  <v-checkbox
                    class="ma-0 pa-0"
                    hide-details
                    v-model="cart.checked"
                    @change="selected($event)"
                  ></v-checkbox>
                  <product-detail-component t-detail-component class="my-3 pa-0">
                    <template v-slot:productImg>
                      <img :src="cart.img" />
                    </template>
                    <template v-slot:productContent>
                      <div class="caption">{{ cart.brand }}</div>
                      <span class="subtitle-2" v-if="byteSize(cart.productName) >= 18"
                        >{{ sliceByByte(cart.productName, 17) }}...</span
                      >
                      <span class="subtitle-2" v-else>{{ cart.productName }}</span>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto">
                            <img :src="cart.colorChip" width="15" />
                          </v-col>
                          <v-spacer></v-spacer>
                          <v-col cols="auto">
                            <span class="caption">{{ cart.psize }}</span>
                          </v-col>
                        </v-row>
                      </v-container>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">₩{{ cart.price.toLocaleString() }}</span>
                          </v-col>
                          <v-spacer />
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">{{ cart.amount }}개</span>
                          </v-col>
                        </v-row>
                      </v-container>
                    </template>
                    <template v-slot:productBtn>
                      <v-btn
                        small
                        color="beige darken-4 white--text"
                        width="60"
                        @click="changeProduct(cart.productDetailNo, cart.colorChip, cart.psize)"
                        >옵션변경</v-btn
                      >
                    </template>
                  </product-detail-component>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </div>
          <v-divider class="mt-10" />

          <!-- 주문 예정 금액-->
          <div class="mx-4">
            <v-container class="py-0">
              <v-row justify="center" class="mt-1">
                <v-col cols="auto">
                  <span class="text-h5">주문예정금액</span>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="auto">
                  <span class="text-h5">₩{{ (totalPrice - totalDiscount).toLocaleString() }}</span>
                </v-col>
              </v-row>
              <v-row justify="center" class="py-0 px-2">
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">총 상품금액</span>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">₩{{ totalPrice.toLocaleString() }}</span>
                </v-col>
              </v-row>
              <v-row justify="center" class="py-0 px-2">
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">프로모션</span>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">₩0</span>
                </v-col>
              </v-row>
              <v-row justify="center" class="py-0 px-2">
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">배송비</span>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="auto" class="ma-0 py-0">
                  <span class="caption" color="grey">₩0</span>
                </v-col>
              </v-row>
            </v-container>
          </div>
          <v-divider class="mt-8" />
          <v-container class="my-7">
            <v-row justify="center">
              <v-col cols="auto">
                <v-btn
                  width="160"
                  height="50"
                  class="text-h5"
                  @click="deleteCart"
                  :disabled="!isActive"
                  :color="`${isActive ? 'beige navy--text' : 'blue-grey lighten-5 black--text'}`"
                >
                  삭제
                </v-btn>
              </v-col>
              <v-col cols="auto">
                <v-btn
                  width="160"
                  height="50"
                  class="text-h5"
                  @click="goOrder"
                  :disabled="!isActive"
                  :color="`${isActive ? 'navy white--text' : 'blue-grey lighten-5 black--text'}`"
                >
                  주문하기
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-tab-item>
      <v-tab-item key="soldOutProduct">
        <v-card flat>
          <!-- 품절상품-->
          <div>
            <v-checkbox
              class="ma-4 pa-0"
              hide-details
              label="전체 선택"
              v-model="allChecked2"
              @click="checkedAll2(allChecked2)"
            ></v-checkbox>
            <v-card
              class="rounded-lg ma-3"
              v-for="(cart, indexOfSoldOutList) in $store.state.cart.soldOutList"
              :key="indexOfSoldOutList"
            >
              <v-list-item three-line>
                <v-list-item-content class="ma-0 my-1 pa-0">
                  <v-checkbox
                    class="ma-0 pa-0"
                    hide-details
                    v-model="cart.checked"
                    v-if="!cart.isAutoPurchase"
                    @change="selected2($event)"
                  ></v-checkbox>
                  <product-detail-component t-detail-component class="my-3 pa-0">
                    <template v-slot:productImg>
                      <img :src="cart.img" />
                    </template>
                    <template v-slot:productContent>
                      <div class="caption">{{ cart.brand }}</div>
                      <span class="subtitle-2" v-if="byteSize(cart.productName) >= 18"
                        >{{ sliceByByte(cart.productName, 17) }}...</span
                      >
                      <span class="subtitle-2" v-else>{{ cart.productName }}</span>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto">
                            <img :src="cart.colorChip" width="15" />
                          </v-col>
                          <v-spacer></v-spacer>
                          <v-col cols="auto">
                            <span class="caption">{{ cart.psize }}</span>
                          </v-col>
                        </v-row>
                      </v-container>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">₩{{ cart.price.toLocaleString() }}</span>
                          </v-col>
                          <v-spacer />
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">{{ cart.amount }}개</span>
                          </v-col>
                        </v-row>
                      </v-container>
                    </template>
                    <template v-slot:productBtn>
                      <span class="mb-0 pb-0">입고알림</span>
                      <v-switch
                        class="mt-0 ml-2 pt-0 pb-4"
                        color="orange"
                        v-model="cart.isNotify"
                        @change="changeNotify(cart)"
                        hide-details
                      ></v-switch>
                      <span class="mb-0 pb-0 pt-4">자동구매</span>
                      <v-switch
                        class="mt-0 ml-2 pt-0"
                        color="success"
                        :readonly="cart.isAutoPurchase"
                        v-model="cart.isAutoPurchase"
                        @change="changeAutoPurchase(indexOfSoldOutList)"
                        hide-details
                      ></v-switch>
                    </template>
                  </product-detail-component>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </div>
          <v-divider class="mt-8" />
          <v-container class="my-7">
            <v-row justify="center">
              <v-col cols="auto">
                <v-btn
                  width="160"
                  height="50"
                  class="text-h5"
                  @click="deleteSoldout"
                  :disabled="!isActive2"
                  :color="`${isActive2 ? 'beige navy--text' : 'blue-grey lighten-5 black--text'}`"
                >
                  삭제
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
    <order-input-modal v-if="autoPurchaseModal">
      <template v-slot:header>
        <span class="font-weight-bold text-h4">자동 구매 안내</span></template
      >
      <template v-slot:body>
        <span class="body-1"
          >자동 구매를 진행하시겠습니까?<br />
          해당 상품이 재입고될 시, 결제가 자동으로 진행됩니다.</span
        ></template
      >
      <template v-slot:footer>
        <v-container class="my-4">
          <v-row>
            <v-col cols="6"></v-col>
            <v-col cols="3">
              <v-btn color="navy white--text" class="ml-4" @click="goOrderAutoPurcahse">확인</v-btn>
            </v-col>
            <v-col cols="3">
              <v-btn color="beige black--text" class="mr-2" @click="canelAutoPurchase">취소</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </template>
    </order-input-modal>
    <!-- 옵션 변경 모달-->
    <order-input-modal v-if="changeOptionModal">
      <template v-slot:header>
        <v-row>
          <v-col>
            <span class="font-weight-bold text-h6">옵션 변경</span>
          </v-col>
          <v-col class="text-right">
            <span color="pink lighten-4 black--text" class="text-h5" @click="closeChangeOptionModal"
              >Ⅹ</span
            >
          </v-col>
        </v-row>
      </template>
      <template v-slot:body>
        <v-stepper v-model="step">
          <v-stepper-header>
            <v-stepper-step
              :complete="step > 1"
              step="1"
              color="navy"
              editable
              edit-icon="mdi-check-outline"
            >
              &nbsp;&nbsp;색상
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step
              :complete="step > 2"
              step="2"
              color="navy"
              editable
              edit-icon="mdi-check-outline"
            >
              &nbsp;&nbsp;사이즈
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3" color="navy" edit-icon>&nbsp;&nbsp;수량 </v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <div class="text-center">
                <span v-for="colorChip in colorOptionList" :key="colorChip">
                  <img
                    :src="colorChip"
                    class="mr-2"
                    width="60"
                    @click="changeColorOption(colorChip)"
                  />
                </span>
              </div>
            </v-stepper-content>

            <v-stepper-content step="2">
              <div class="text-center">
                <span v-for="psize in psizeOptionList" :key="psize">
                  <v-btn
                    small
                    class="mr-2 text-h6"
                    color="beige black--text"
                    @click="changePsizeOption(psize)"
                    >{{ psize }}</v-btn
                  >
                </span>
              </div>
            </v-stepper-content>

            <v-stepper-content step="3">
              <v-col class="text-right">
                <div class="text-center">
                  <v-btn x-small color="beige black--text" @click="decreaseSum" class="mb-2">
                    <v-icon> mdi-minus-thick </v-icon>
                  </v-btn>
                  <span class="ml-3 mr-3 text-h5">{{ changedAmount }}</span>
                  <v-btn x-small color="beige black--text" @click="increaseSum" class="mb-2">
                    <v-icon> mdi-plus-thick </v-icon>
                  </v-btn>
                </div>
                <v-btn small color="navy white--text" @click="applyChangedProduct"> 적용 </v-btn>
              </v-col>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </template>
    </order-input-modal>
  </div>
</template>

<script>
import api from "@/apis/order";
import ProductDetailComponent from "@/components/order/ProductDetailComponent.vue";
import OrderInputModal from "@/components/order/OrderInputModal.vue";

export default {
  name: "Cart",
  components: { ProductDetailComponent, OrderInputModal },
  data: () => ({
    tab: null,
    items: ["판매상품", "품절상품"],
    readonly: false,
    allChecked: true,
    allChecked2: true,
    autoPurchaseModal: false,
    changeOptionModal: false,
    totalPrice: 0,
    totalDiscount: 0,
    indexOfSoldOutList: null,
    step: 1, // stepper
    colorOptionList: null,
    psizeOptionList: null,
    optionList: null,
    changingProductDetailNo: null, // 바뀌기전
    changingPsize: null, // 바뀌기전
    changedColor: null,
    changedPsize: null,
    changedAmount: 0,
    optionStock: 0,
  }),
  methods: {
    byteSize(str) {
      return new Blob([str]).size;
    },
    sliceByByte(str, maxByte) {
      var b, i, c;
      for (b = i = 0; (c = str.charCodeAt(i)); ) {
        b += c >> 7 ? 2 : 1;
        if (b > maxByte) break;
        i++;
      }
      return str.substring(0, i);
    },
    checkedAll(checked) {
      this.allChecked = checked;
      var sum = 0;
      for (var cart of this.$store.state.cart.cartList) {
        cart.checked = checked;
        sum += cart.price * cart.amount;
      }
      if (checked) this.totalPrice = sum;
      else this.totalPrice = 0;
    },
    checkedAll2(checked) {
      this.allChecked2 = checked;
      for (var cart of this.$store.state.cart.soldOutList) {
        if (cart.isAutoPurchase) continue;
        cart.checked = checked;
      }
    },
    selected(e) {
      var sum = 0;
      var chk = true;
      for (var cart of this.$store.state.cart.cartList) {
        if (!cart.checked) {
          chk = false;
        } else {
          sum += cart.price * cart.amount;
        }
      }
      this.totalPrice = sum;
      this.allChecked = chk;
    },
    selected2(e) {
      var chk = true;
      for (var cart of this.$store.state.cart.soldOutList) {
        if (cart.isAutoPurchase) continue;
        if (!cart.checked) {
          chk = false;
          break;
        }
      }
      this.allChecked2 = chk;
    },
    goOrder() {
      var cartList = new Array();
      for (var cart of this.$store.state.cart.cartList) {
        if (cart.isAutoPurchase) continue;
        if (cart.checked) {
          var cartObject = new Object();
          cartObject.productDetailNo = cart.productDetailNo;
          cartObject.amount = cart.amount;
          cartObject.price = cart.price;
          cartObject.psize = cart.psize;
          cartObject.colorChip = cart.colorChip;
          cartObject.brand = cart.brand;
          cartObject.productName = cart.productName;
          cartObject.img = cart.img;
          cartList.push(cartObject);
        }
      }
      this.$store.dispatch("cart/setCartListByAsync", cartList).catch((err) => {
        console.log(err);
      });
      this.$router.push("/order/orderForm");
    },
    deleteCart() {
      var cartList = new Array();
      for (var cart of this.$store.state.cart.cartList) {
        if (cart.checked) {
          let cartObject = new Object();
          cartObject.productDetailNo = cart.productDetailNo;
          cartObject.memberId = this.$store.state.userId;
          cartObject.psize = cart.psize;
          api.deleteCart(cartObject);
        } else {
          let cartObject = new Object();
          cartObject.productDetailNo = cart.productDetailNo;
          cartObject.amount = cart.amount;
          cartObject.price = cart.price;
          cartObject.psize = cart.psize;
          cartObject.colorChip = cart.colorChip;
          cartObject.brand = cart.brand;
          cartObject.productName = cart.productName;
          cartObject.img = cart.img;
          cartObject.checked = cart.checked;
          cartList.push(cartObject);
        }
      }
      this.$store.dispatch("cart/setCartListByAsync", cartList);
    },
    deleteSoldout() {
      var soldOutList = new Array();
      for (var cart of this.$store.state.cart.soldOutList) {
        if (cart.checked) {
          let cartObject = new Object();
          cartObject.productDetailNo = cart.productDetailNo;
          cartObject.memberId = this.$store.state.userId;
          cartObject.psize = cart.psize;
          api.deleteCart(cartObject);
        } else {
          let cartObject = new Object();
          cartObject.productDetailNo = cart.productDetailNo;
          cartObject.amount = cart.amount;
          cartObject.price = cart.price;
          cartObject.psize = cart.psize;
          cartObject.colorChip = cart.colorChip;
          cartObject.brand = cart.brand;
          cartObject.productName = cart.productName;
          cartObject.img = cart.img;
          cartObject.checked = cart.checked;
          cartObject.isAutoPurchase = cart.isAutoPurchase;
          cartObject.isNotify = cart.isNotify;
          soldOutList.push(cartObject);
        }
      }
      this.$store.dispatch("cart/setSoldOutListByAsync", soldOutList);
    },
    changeNotify(cart) {
      cart.memberId = this.$store.state.userId;
      if (cart.isNotify) {
        cart.isNotify = 1;
      } else {
        cart.isNotify = 0;
      }
      if (cart.isAutoPurchase) {
        // 데이터형 변환
        cart.isAutoPurchase = 1;
      } else {
        cart.isAutoPurchase = 0;
      }
      api.updateIsNotify(cart);
    },
    changeAutoPurchase(idx) {
      this.indexOfSoldOutList = idx;
      this.autoPurchaseModal = true;
    },
    canelAutoPurchase() {
      this.autoPurchaseModal = false;
      this.$store.state.cart.soldOutList[this.indexOfSoldOutList].isAutoPurchase = false;
    },
    goOrderAutoPurcahse() {
      this.autoPurchaseModal = false;
      var cartList = new Array();
      var cartObject = this.$store.state.cart.soldOutList[this.indexOfSoldOutList];
      cartList.push(cartObject);
      this.$store.dispatch("cart/setCartListByAsync", cartList);
      this.$router.push("/order/orderForm");
    },
    changeProduct(productDetailNo, colorChip, psize) {
      this.changingProductDetailNo = productDetailNo;
      this.changingPsize = psize;
      this.changedColor = colorChip;
      this.changedPsize = psize;
      this.changedAmount = 1;
      api.getOptionList(productDetailNo).then((response) => {
        this.colorOptionList = response.data.colorChipSet;
        this.psizeOptionList = response.data.psizeList;
        this.optionList = response.data.optionList;
      });
      this.changeOptionModal = true;
    },
    closeChangeOptionModal() {
      this.changeOptionModal = false;
      this.changedColor = null;
      this.changedPsize = null;
      this.optionStock = null;
      this.step = 1;
    },
    applyChangedProduct() {
      let img = "";
      let changedProductDetailNo = "";
      for (let option of this.optionList) {
        if (option.psize === this.changedPsize && option.colorChip === this.changedColor) {
          img = option.img;
          changedProductDetailNo = option.productDetailNo;
          break;
        }
      }
      let cart = new Object();
      cart.productDetailNo = this.changingProductDetailNo; // 바뀌기전
      cart.psize = this.changingPsize; // 바뀌기전
      cart.changedProductDetailNo = changedProductDetailNo;
      cart.colorChip = this.changedColor;
      cart.changedPsize = this.changedPsize;
      cart.img = img;
      cart.amount = this.changedAmount;

      api.updateOption(cart).then(() => {
        this.getCartList();
      });

      this.changeOptionModal = false;
      this.step = 1;
    },

    changeColorOption(color) {
      this.changedColor = color;
      this.step = 2;
    },
    changePsizeOption(psize) {
      this.changedPsize = psize;

      for (let product of this.optionList) {
        if (product.psize === this.changedPsize && product.colorChip === this.changedColor) {
          this.optionStock = product.amount;
          break;
        }
      }
      this.step = 3;
    },

    increaseSum() {
      if (this.changedAmount < this.optionStock) this.changedAmount += 1;
    },
    decreaseSum() {
      if (this.changedAmount > 1) this.changedAmount -= 1;
    },

    getCartList() {
      api.getCartList().then((response) => {
        var cartList = new Array();
        var soldOutList = new Array();
        this.totalPrice = 0;
        for (var cart of response.data) {
          if (cart.stock >= cart.amount && cart.stock != 0) {
            let cartObject = new Object();
            cartObject.productDetailNo = cart.productDetailNo;
            cartObject.amount = cart.amount;
            cartObject.price = cart.price;
            cartObject.psize = cart.psize;
            cartObject.colorChip = cart.colorChip;
            cartObject.brand = cart.brand;
            cartObject.productName = cart.productName;
            cartObject.img = cart.img;
            cartObject.checked = cart.checked;
            cartList.push(cartObject);
            this.totalPrice += cart.price * cart.amount;
          } else {
            let soldOutObject = new Object();
            soldOutObject.productDetailNo = cart.productDetailNo;
            soldOutObject.amount = cart.amount;
            soldOutObject.price = cart.price;
            soldOutObject.psize = cart.psize;
            soldOutObject.colorChip = cart.colorChip;
            soldOutObject.brand = cart.brand;
            soldOutObject.productName = cart.productName;
            soldOutObject.img = cart.img;
            soldOutObject.isNotify = cart.isNotify;
            soldOutObject.isAutoPurchase = cart.isAutoPurchase ? true : false;
            if (soldOutObject.isAutoPurchase) soldOutObject.checked = false;
            else soldOutObject.checked = cart.checked;
            soldOutList.push(soldOutObject);
          }
        }
        this.$store.dispatch("cart/setCartListByAsync", cartList);
        this.$store.dispatch("cart/setSoldOutListByAsync", soldOutList);
      });
    },
  },
  created() {
    this.getCartList();
  },
  computed: {
    isActive() {
      if (this.totalPrice != 0) return true;
      return false;
    },
    isActive2() {
      var chk = false;
      for (var cart of this.$store.state.cart.soldOutList) {
        if (cart.isAutoPurchase) continue;
        if (cart.checked) {
          chk = true;
          break;
        }
      }
      return chk;
    },
  },
};
</script>

