              </v-row>
            </v-container>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
      <v-divider />
      <!-- 결제 방법-->
      <v-expansion-panels v-model="panel3" :readonly="readonly" multiple class="mt-3 mb-7">
        <v-expansion-panel>
