                      <order-input-component :padding="0" :virtualBankComponent="true">
                        <template v-slot:inputTitle>
                          <span
                            class="font-weight-bold"
                            color="text-body1 grey"
                            style="font-size:15px; font-family='hyundai"
                            v-if="virtualBank.bank == null"
                            >가상계좌를 선택하세요.</span
                          >
                          <span
                            color="text-body1 grey"
                            style="font-size:15px; font-family='hyundai; font-weight:800"
                            v-if="virtualBank.bank !== null"
                            >{{ virtualBank.bank + ": " + virtualBank.account }}
                          </span>
                        </template>
                        <template v-slot:inputContent> </template>
                        <template v-slot:inputBtn>
                          <bottom-sheet-component
