<script>
import api from "@/apis/order";
import apiMember from "@/apis/member";
import apiCoupon from "@/apis/coupon";
import ProductDetailComponent from "@/components/order/ProductDetailComponent.vue";
import OrderInputComponent from "@/components/order/OrderInputComponent.vue";
import RefundInfo from "./RefundInfo.vue";
import BottomSheetComponent from "@/components/order/BottomSheetComponent.vue";

export default {
  name: "OrderForm",
  components: { ProductDetailComponent, OrderInputComponent, RefundInfo, BottomSheetComponent },
  data: () => ({
    panel: [0, 1],
    panel2: [0, 1],
    panel3: [0, 1],
    readonly: false,
    brandName: "청바지바지신발양말",
    shippingSheet: false,
    shipping: {
      post: null,
      addr: null,
      detail: null,
    },
    shippingTiles: [
      { img: "home.jpeg", title: "일산동구 정발산동", detail: "206호", post: "12345" },
      { img: "home.jpeg", title: "관악구 신림동", detail: "1708호", post: "18597" },
      { img: "company.png", title: "서울특별시 송파구 it벤처타워", detail: "12층", post: "95214" },
    ],
    refundInfo: false,
    refund: {
      bank: "국민은행",
      account: "65848204875845",
      name: "양승완",
    },
    payment: {
      simple: false,
      card: false,
      virtual: false,
    },
    virtualBank: {
      bank: null,
      account: null,
    },
    virtualBankTiles: [
      { img: "kb.jpeg", title: "국민은행", account: null },
      { img: "woori.jpeg", title: "우리은행", account: null },
      { img: "shinhan.jpeg", title: "신한은행", account: null },
      { img: "hana.jpeg", title: "하나은행", account: null },
    ],
    usePoint: 0,
    userPoint: 0,
    paymentSheet: false,
    paymentType: -1,
    otp: "",
    otpLength: 6,
    isFocused: false,
    usedCoupon: [],
    member: {},
    order: {
      memberId: null,
      request: null,
      addr: null,
      detailAddr: null,
      zipCode: null,
      tel: null,
      phone: null,
      recName: null,
      detailList: [],
      paymentList: [],
    },
    totalPrice: 0,
    totalDiscount: 0,
    totalCoupon: 0,
    couponPriceArr: [], // 쿠폰 가격 저장 {couponNo: "", totalDiscountPrice:숫자}
  }),
  methods: {
    useAllPoint() {
      this.usePoint = this.userPoint;
    },
    changeUsePoint() {
      if (this.usePoint > this.userPoint) {
        this.$nextTick(() => {
          this.usePoint = this.userPoint;
        });
      }
    },
    getCouponList(cart, idx) {
      apiCoupon.getCoupon(cart.brand, cart.price).then((response) => {
        var couponArr = new Array();
        for (let coupon of response.data) {
          let chk = true;
          for (var useCoupon of this.usedCoupon) {
            if (coupon.coupon_no === useCoupon) {
              chk = false;
              break;
            }
          }
          if (chk) couponArr.push(coupon);
        }

        for (let coupon of couponArr) {
          coupon.usedIdx = idx;
        }
        this.$store.state.order.couponList = couponArr;
      });
    },
    getCouponInfo(coupon) {
      let cartList = [];
      let idx = 0;

      for (let cart of this.$store.state.cart.cartList) {
        let newCart = cart;
        if (coupon.usedIdx == idx) {
          newCart.appliedCoupon = true;
          newCart.couponNo = coupon.coupon_no;
          newCart.couponDiscount = coupon.total_discount_price;
          this.totalCoupon += coupon.total_discount_price;
          var couponPriceObject = {
            couponNo: coupon.coupon_no,
            totalDiscountPrice: coupon.total_discount_price,
          };
          this.couponPriceArr.push(couponPriceObject);
        }
        idx++;
        cartList.push(newCart);
      }
      this.usedCoupon.push(coupon.coupon_no);
      this.$store.state.cart.cartList = cartList;
    },
    cancelAppiedCoupon(index) {
      //총 쿠폰 사용 가격 수정
      for (let coupon of this.usedCoupon) {
        for (var couponPrice of this.couponPriceArr) {
          if (
            coupon === couponPrice.couponNo &&
            this.$store.state.cart.cartList[index].couponNo == coupon
          ) {
            this.totalCoupon -= couponPrice.totalDiscountPrice;
            break;
          }
        }
      }

      // usedCoupon갱신
      var tempArr = new Array();
      for (let coupon of this.usedCoupon) {
        if (this.$store.state.cart.cartList[index].couponNo !== coupon) {
          tempArr.push(coupon);
        }
      }
      this.usedCoupon = tempArr;

      // 쿠폰 회수
      let cartList = [];
      let idx = 0;
      for (let cart of this.$store.state.cart.cartList) {
        let newCart = cart;
        if (index == idx) {
          newCart.appliedCoupon = false;
          newCart.couponNo = null;
          newCart.couponDiscount = 0;
        }
        idx++;
        cartList.push(newCart);
      }
      this.$store.state.cart.cartList = cartList;
    },
    byteSize(str) {
      return new Blob([str]).size;
    },
    sliceByByte(str, maxByte) {
      var b, i, c;
      for (b = i = 0; (c = str.charCodeAt(i)); ) {
        b += c >> 7 ? 2 : 1;
        if (b > maxByte) break;
        i++;
      }
      return str.substring(0, i);
    },

    insertOrderInfo(type) {
      this.order.memberId = this.$store.state.userId;
      this.order.addr = this.shipping.addr;
      this.order.detailAddr = this.shipping.detail;
      this.order.zipCode = this.shipping.post;
      this.order.tel = "010-1234-5678";
      this.order.phone = "010-1234-5678";
      this.order.recName = this.member.name;
      this.order.request = "부재중시 연락주세요.";
      this.order.priceTotal = this.totalPrice;
      this.order.priceDiscount = parseInt(this.usePoint) + this.totalCoupon;

      for (let cart of this.$store.state.cart.cartList) {
        let orderDetail = new Object();
        orderDetail.productDetailNo = cart.productDetailNo;
        orderDetail.amount = cart.amount;
        orderDetail.price = cart.price;
        if (cart.isAutoPurchase) orderDetail.state = 7;
        else if (type === "신용카드") orderDetail.state = 2;
        else orderDetail.state = 1;
        orderDetail.psize = cart.psize;
        orderDetail.discount = null;
        orderDetail.couponNo = cart.couponNo;
        orderDetail.colorChip = cart.colorChip;
        orderDetail.brand = cart.brand;
        orderDetail.productName = cart.productName;
        orderDetail.img = cart.img;
        if (parseInt(this.usePoint) > 0)
          orderDetail.point = parseInt(this.usePoint) / this.$store.state.cart.cartList.length;
        if (orderDetail.couponNo !== null) {
          orderDetail.couponDiscount = cart.couponDiscount;
        }
        this.order.detailList.push(orderDetail);
      }

      if (type === "무통장입금") {
        let payment = new Object();
        payment.paymentType = type;
        payment.payAccount = this.virtualBank.account;
        payment.payPrice = this.totalPrice - parseInt(this.usePoint) - this.totalCoupon;
        payment.company = this.virtualBank.bank;
        payment.installment = "일시불";
        this.order.paymentList.push(payment);
      } else {
        let payment = new Object();
        payment.paymentType = type;
        payment.payAccount = "1597-4261-3584-5383";
        payment.payPrice = this.totalPrice - parseInt(this.usePoint) - this.totalCoupon;
        payment.company = "우리카드";
        payment.installment = "일시불";
        this.order.paymentList.push(payment);
      }

      //포인트 사용 내역
      if (parseInt(this.usePoint) > 0) {
        let payment = new Object();
        payment.paymentType = "포인트";
        payment.payPrice = parseInt(this.usePoint);
        this.order.paymentList.push(payment);
      }
      // 쿠폰 사용 내역{
      if (this.totalCoupon > 0) {
        let payment = new Object();
        payment.paymentType = "쿠폰";
        payment.payPrice = this.totalCoupon;
        this.order.paymentList.push(payment);
      }

      // 자동결제 예정
      let cart = this.$store.state.cart.cartList[0];
      if (cart.isAutoPurchase) {
        cart.memberId = this.$store.state.userId;
        cart.isAutoPurchase = 1;
        api.updateIsAutoPurchase(cart).catch((err) => {
          return;
        });
      }

      api.insertOrder(this.order).then((response) => {
        let orderInfo = new Object();
        orderInfo.orderNo = response.data.orderNo;
        orderInfo.payTotalPrice = this.totalPrice - parseInt(this.usePoint) - this.totalCoupon;
        orderInfo.earningPoint = response.data.earningPoint;
        orderInfo.addr = this.order.addr;
        orderInfo.detailAddr = this.order.detailAddr;
        orderInfo.phone = this.order.phone;

        this.$router.push("/order/orderFinish");
        this.$store.dispatch("order/setOrderInfoByAsync", orderInfo);
        this.$store.dispatch("cart/setCartListByAsync", []);
      });
    },
    getShippingInfo(shipping) {
      this.shipping.post = shipping.post;
      this.shipping.addr = shipping.title;
      this.shipping.detail = shipping.detail;
      this.shippingSheet = false;
    },
    getVirtualBankInfo(virtualBank) {
      this.virtualBank.bank = virtualBank.title;
      this.virtualBank.account = virtualBank.account;
    },
    getRefund(refund) {
      this.refund.bank = refund.bank;
      this.refund.account = refund.account;
      this.refund.name = refund.name;
      this.refundInfo = false;
    },
    clickSimple() {
      this.payment.simple = true;
      this.payment.card = false;
      this.payment.virtual = false;
      this.paymentType = 0;
    },
    clickCard() {
      this.payment.simple = false;
      this.payment.card = true;
      this.payment.virtual = false;
      this.paymentType = 1;
    },
    clickVirtual() {
      this.payment.card = false;
      this.payment.simple = false;
      this.payment.virtual = true;
      this.paymentType = 2;
    },
    cancelPayment() {
      this.paymentSheet = false;
      this.virtualBank.bank = null;
      this.virtualBank.account = null;
    },
  },
  computed: {
    isActivePayment() {
      if (this.shipping.post !== null) {
        if (this.payment.simple || this.payment.card || this.payment.virtual) return true;
      }
      return false;
    },
    isActivePaymentFinish() {
      return this.otp.length === this.otpLength;
    },
    isActiveOrderFinish() {
      return this.virtualBank.bank !== null;
    },
  },
  created() {
    apiMember.getMember().then((response) => {
      this.member = response.data;
    });
    api.getUserPoint().then((response) => {
      this.userPoint = response.data.userPoint;
    });
    var sum = 0;
    for (let cart of this.$store.state.cart.cartList) {
      sum += cart.price * cart.amount;
      cart.appliedCoupon = false;
      cart.couponNo = null;
      cart.copounDiscount = 0;
    }
    this.totalPrice = sum;

    // 가상계좌 생성
    for (let virtualBank of this.virtualBankTiles) {
      let account = "";
      for (let i = 0; i < 12; i++) account += String(Math.floor(Math.random() * 10));
      virtualBank.account = account;
    }
  },
};
</script>

