export default {
  name: "BottomSheetComponent",
  components: {},
  data() {
    return {
      sheet: false,
    };
  },
  methods: {},
  props: [
    "btnColor",
    "btnContent",
    "title",
    "tiles",
    "btnIcon",
    "btnWidth",
    "btnHeight",
    "couponBottomSheet",
    "virtualBankBottomSheet",
    "idx",
    "marginTop",
  ],
};
</script>

