<template>
  <div height="926" width="428">
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">주문 내역</span>
    </v-card-title>
    <div>
      <v-container class="py-0 my-0">
        <v-row justify="center" class="my-0 py-0">
          <v-col class="text-left mt-7">
            <span class="ma-0 pa-0 mt-3" style="font-weight: 100; color: grey" v-if="during === 1"
              >최근 일주일 목록</span
            >
            <span
              class="ma-0 pa-0 mt-3"
              style="font-weight: 100; color: grey"
              v-else-if="during === 2"
              >최근 1개월 목록</span
            >
            <span
              class="ma-0 pa-0 mt-3"
              style="font-weight: 100; color: grey"
              v-else-if="during === 3"
              >최근 3개월 목록</span
            >
            <span
              class="ma-0 pa-0 mt-3"
              style="font-weight: 100; color: grey"
              v-else-if="during === 4"
              >최근 6개월 목록</span
            >
          </v-col>
          <v-spacer class="my-0 py-0"></v-spacer>
          <v-col cols="auto" class="my-0 py-0">
            <div class="text-center">
              <bottom-sheet-component
                btnColor="beige black--text"
                btnIcon="mdi-calendar-search"
                title="기간 선택"
                :tiles="tiles"
                btnWidth="80"
                btnHeight="40"
                @getInfo="getOrderList"
              ></bottom-sheet-component>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <div>
      <v-expansion-panels class="px-3" v-model="panel" multiple>
        <v-expansion-panel
          v-for="order in $store.state.order.orderList"
          :key="order.orderNo"
          class="mt-10"
        >
          <v-expansion-panel-header class="ma-0 py-0 px-3">
            <v-container class="mt-3 pa-2">
              <v-row justify="center">
                <v-col cols="auto" class="pt-0">
                  <span class="text-body-2 font-weight-bold">{{ order.orderNo }}</span>
                </v-col>
                <v-spacer />
                <v-col cols="auto" class="pa-0 mr-2">
                  <span class="text-body-2 grey--text font-weight-bold">{{ order.orderDate }}</span>
                </v-col>
              </v-row>
            </v-container>
          </v-expansion-panel-header>
          <v-divider />
          <v-expansion-panel-content>
            <v-card class="mx-0 mt-2">
              <v-list-item three-line>
                <v-list-item-content class="my-3 pa-0">
                  <product-detail-component
                    v-for="orderDetail in order.detailList"
                    :key="orderDetail.orderDetailNo"
                    class="mt-2"
                  >
                    <template v-slot:productImg>
                      <img :src="orderDetail.img" />
                    </template>
                    <template v-slot:productContent>
                      <div class="caption">{{ orderDetail.brand }}</div>
                      <span class="subtitle-2" v-if="byteSize(orderDetail.productName) >= 18"
                        >{{ sliceByByte(orderDetail.productName, 17) }}...</span
                      >
                      <span class="subtitle-2" v-else>{{ orderDetail.productName }}</span>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto">
                            <img :src="orderDetail.colorChip" width="15" />
                          </v-col>
                          <v-spacer></v-spacer>
                          <v-col cols="auto">
                            <span class="caption">{{ orderDetail.psize }}</span>
                          </v-col>
                        </v-row>
                      </v-container>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto" class="pt-0">
                            <span class="caption"
                              >₩{{
                                (orderDetail.price - orderDetail.discount).toLocaleString()
                              }}</span
                            >
                          </v-col>
                          <v-spacer />
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">{{ orderDetail.amount }}개</span>
                          </v-col>
                        </v-row>
                      </v-container>
                    </template>
                    <template v-slot:productBtn>
                      <span class="caption ml-1" v-if="orderDetail.state == 1">입금대기</span>
                      <span class="caption ml-1" v-if="orderDetail.state == 2">주문완료</span>
                      <span class="caption" v-if="orderDetail.state == 3">배송준비</span>
                      <span class="caption" v-if="orderDetail.state == 4">배송중</span>
                      <span class="caption" v-if="orderDetail.state == 5">배송완료</span>
                      <span class="caption" v-if="orderDetail.state == 6">주문취소</span>
                      <span class="caption ml-1" v-if="orderDetail.state == 7">결제예정</span>
                      <v-btn
                        small
                        width="30"
                        class="mr-7 pink lighten-5"
                        v-if="orderDetail.state == 2 || orderDetail.state == 1"
                        @click="openCancelOrderModal(order.orderNo, orderDetail)"
                        >취소</v-btn
                      >
                    </template>
                  </product-detail-component>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-expansion-panel-content>
          <v-divider />
          <v-expansion-panel-content>
            <v-container class="px-0 py-0">
              <v-row justify="center" class="py-2">
                <v-col class="ml-3">
                  <span class="text-right text-body-2 font-weight-bold"
                    >총 금액: ₩{{
                      (order.priceTotal - order.priceDiscount).toLocaleString()
                    }}원</span
                  >
                </v-col>
                <v-col class="text-right"
                  ><v-btn
                    small
                    color="beige"
                    width="60"
                    class="mr-7"
                    :to="`/order/orderDetail?orderNo=${order.orderNo}`"
                    >상세조회</v-btn
                  ></v-col
                >
              </v-row>
            </v-container>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
    <order-input-modal v-if="cancelOrderModal">
      <template v-slot:header> <span class="font-weight-bold text-h4">주문 취소</span></template>
      <template v-slot:body>
        <span class="body-1">해당 제품 주문을 취소 하시겠습니까?</span></template
      >
      <template v-slot:footer>
        <v-container class="my-4">
          <v-row>
            <v-col cols="6"></v-col>
            <v-col cols="3">
              <v-btn color="navy white--text" class="ml-4" @click="cancelOrder">확인</v-btn>
            </v-col>
            <v-col cols="3">
              <v-btn color="beige black--text" class="mr-2" @click="cancelOrderModal = false"
                >취소</v-btn
              >
            </v-col>
          </v-row>
        </v-container>
      </template>
    </order-input-modal>
  </div>
</template>

<script>
import api from "@/apis/order";
import ProductDetailComponent from "@/components/order/ProductDetailComponent.vue";
import BottomSheetComponent from "@/components/order/BottomSheetComponent.vue";
import OrderInputModal from "@/components/order/OrderInputModal.vue";
import dayjs from "dayjs";

export default {
  name: "OrderList",
  components: { ProductDetailComponent, BottomSheetComponent, OrderInputModal },
  component: {
    dayjs,
  },
  data: () => ({
    duringSheet: false,
    panel: [0, 1, 2, 3, 4],
    readonly: false,
    tempOrderNo: null,
    tempOrderDetail: null,
    cancelOrderModal: false,
    during: 1,
    tiles: [
      { img: "week.png", title: "1주일" },
      { img: "1month.png", title: "1개월" },
      { img: "3month.png", title: "3개월" },
      { img: "6month.png", title: "6개월" },
    ],
  }),
  methods: {
    byteSize(str) {
      return new Blob([str]).size;
    },
    sliceByByte(str, maxByte) {
      var b, i, c;
      for (b = i = 0; (c = str.charCodeAt(i)); ) {
        b += c >> 7 ? 2 : 1;

        if (b > maxByte) break;

        i++;
      }
      return str.substring(0, i);
    },
    getOrderList(arg) {
      if (arg.title == "1주일") {
        this.during = 1;
      } else if (arg.title == "1개월") {
        this.during = 2;
      } else if (arg.title == "3개월") {
        this.during = 3;
      } else if (arg.title == "6개월") {
        this.during = 4;
      }
      api.getOrderList(this.during).then((response) => {
        console.log(response.data);
        this.$store.state.order.orderList = response.data.orderList;
        for (var order of this.$store.state.order.orderList) {
          order.orderDate = dayjs(order.orderDate).format("YYYY-MM-DD HH:mm:ss");
        }
      });
      this.duringSheet = false;
    },
    openCancelOrderModal(orderNo, orderDetail) {
      this.cancelOrderModal = true;
      this.tempOrderNo = orderNo;
      this.tempOrderDetail = orderDetail;
    },
    cancelOrder() {
      this.tempOrderDetail.orderNo = this.tempOrderNo;
      console.log(this.tempOrderDetail);
      api.cancelOrder(this.tempOrderDetail).then((response) => {
        this.getOrderList(0);
      });
      this.cancelOrderModal = false;
    },
  },
  created() {
    this.getOrderList(0);
  },
};
</script>

<style scoped>
.v-text-field {
  width: 100px;
}
>>> .v-expansion-panel-content__wrap {
  padding: 4px !important;
}
</style>
