<template>
  <div>
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">주문 상세</span>
    </v-card-title>
    <div>
      <v-container class="py-6">
        <v-row justify="center" class="py-0 px-2">
          <v-col cols="auto" class="ma-0 py-0">
            <span class="font-weight-bold body-2">{{ $store.state.order.order.orderNo }}</span>
          </v-col>
          <v-spacer></v-spacer>
          <v-col cols="auto" class="ma-0 py-0">
            <span class="font-weight-bold body-2 amber--text text-accent-1">{{
              $store.state.order.order.orderDate
            }}</span>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <!-- 상품 정보 -->
    <div class="mx-4 mt-4">
      <span class="font-weight-bold body-1">상품 정보</span>
      <v-card class="rounded-lg mx-0 mt-2">
        <v-simple-table>
          <template>
            <tbody>
              <tr
                v-for="orderDetail in $store.state.order.order.detailList"
                :key="orderDetail.orderDetailNo"
              >
                <td class="pa-4">
                  <product-detail-component t-detail-component>
                    <template v-slot:productImg>
                      <img :src="orderDetail.img" />
                    </template>
                    <template v-slot:productContent>
                      <div class="caption">{{ orderDetail.brand }}</div>
                      <span class="subtitle-2" v-if="byteSize(orderDetail.productName) >= 18"
                        >{{ sliceByByte(orderDetail.productName, 17) }}...</span
                      >
                      <span class="subtitle-2" v-else>{{ orderDetail.productName }}</span>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto">
                            <img :src="orderDetail.colorChip" width="15" />
                          </v-col>
                          <v-spacer></v-spacer>
                          <v-col cols="auto">
                            <span class="caption">{{ orderDetail.psize }}</span>
                          </v-col>
                        </v-row>
                      </v-container>
                      <v-container class="ma-0 pa-0">
                        <v-row justify="center">
                          <v-col cols="auto" class="pt-0">
                            <span class="caption"
                              >₩{{
                                (orderDetail.price - orderDetail.discount).toLocaleString()
                              }}</span
                            >
                          </v-col>
                          <v-spacer />
                          <v-col cols="auto" class="pt-0">
                            <span class="caption">{{ orderDetail.amount }}개</span>
                          </v-col>
                        </v-row>
                      </v-container>
                    </template>
                    <template v-slot:productBtn>
                      <span class="caption" v-if="orderDetail.state == 1">입금대기</span>
                      <span class="caption" v-if="orderDetail.state == 2">주문완료</span>
                      <span class="caption" v-if="orderDetail.state == 3">배송준비</span>
                      <span class="caption" v-if="orderDetail.state == 4">배송중</span>
                      <span class="caption" v-if="orderDetail.state == 5">배송완료</span>
                      <span class="caption" v-if="orderDetail.state == 6">주문취소</span>
                      <span class="caption" v-if="orderDetail.state == 7">결제예정</span>
                      <v-btn
                        small
                        width="30"
                        class="mr-7 pink lighten-5"
                        v-if="orderDetail.state == 2 || orderDetail.state == 1"
                        @click="openCancelOrderModal($store.state.order.order.orderNo, orderDetail)"
                        >취소</v-btn
                      >
                    </template>
                  </product-detail-component>
                </td>
              </tr>
              <tr>
                <td class="caption text-right" text>
                  총 금액 : ₩{{ $store.state.order.order.priceTotal.toLocaleString() }}
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </div>
    <v-divider class="mt-5" />
    <!-- 배송지 정보-->
    <div class="mx-4 mt-4">
      <span class="font-weight-bold body-1">배송지 정보</span>
      <v-card class="rounded-lg mx-0 mt-2">
        <v-simple-table>
          <template v-slot:default>
            <tbody>
              <tr>
                <td class="caption" width="100">이름</td>
                <td class="caption">{{ $store.state.order.order.recName }}</td>
              </tr>
              <tr>
                <td class="caption">연락처</td>
                <td class="caption">{{ $store.state.order.order.phone }}</td>
              </tr>
              <tr>
                <td class="caption">배송지</td>
                <td class="caption">
                  ({{ $store.state.order.order.zipCode }})
                  {{ $store.state.order.order.addr }}
                  {{ $store.state.order.order.detailAddr }}
                </td>
              </tr>
              <tr>
                <td class="caption">배송메시지</td>
                <td class="caption">{{ $store.state.order.order.request }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </div>
    <v-divider class="mt-5" />
    <!-- 결제 정보-->
    <div class="mx-4 my-4">
      <span class="font-weight-bold body-1">결제 정보</span>
      <v-card class="rounded-lg mx-0 mt-2">
        <v-simple-table>
          <template v-slot:default>
            <tbody>
              <tr>
                <td class="caption" width="100">주문자</td>
                <td class="caption">{{ $store.state.order.order.recName }}</td>
              </tr>
              <tr
                v-for="payment in $store.state.order.order.paymentList"
                :Key="payment.paymentType"
                v-if="payment.paymentType == '신용카드'"
              >
                <td class="caption">결제수단</td>
                <td class="caption">
                  {{ payment.company }} {{ payment.payAccount }} ({{ payment.installment }})
                </td>
              </tr>
              <tr
                v-for="payment in $store.state.order.order.paymentList"
                :Key="payment.paymentType"
                v-if="payment.paymentType != '신용카드'"
              >
                <td class="caption">할인({{ payment.paymentType }})</td>
                <td class="caption">₩{{ parseInt(payment.payPrice).toLocaleString() }}</td>
              </tr>
              <tr>
                <td class="caption">총 결제금액</td>
                <td class="caption py-3">
                  상품 합계: ₩{{ $store.state.order.order.priceTotal.toLocaleString() }}<br />
                  할인 합계: -₩{{ $store.state.order.order.priceDiscount.toLocaleString() }}<br />
                  최종 결제: ₩{{
                    (
                      $store.state.order.order.priceTotal - $store.state.order.order.priceDiscount
                    ).toLocaleString()
                  }}<br />
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </div>
    <order-input-modal v-if="cancelOrderModal">
      <template v-slot:header> <span class="font-weight-bold text-h4">주문 취소</span></template>
      <template v-slot:body>
        <span class="body-1">해당 제품 주문을 취소 하시겠습니까?</span></template
      >
      <template v-slot:footer>
        <v-container class="my-4">
          <v-row>
            <v-col cols="6"></v-col>
            <v-col cols="3">
              <v-btn color="navy white--text" class="ml-4" @click="cancelOrder">확인</v-btn>
            </v-col>
            <v-col cols="3">
              <v-btn color="beige black--text" class="mr-2" @click="cancelOrderModal = false"
                >취소</v-btn
              >
            </v-col>
          </v-row>
        </v-container>
      </template>
    </order-input-modal>
  </div>
</template>

<script>
import api from "@/apis/order";
import ProductDetailComponent from "@/components/order/ProductDetailComponent.vue";
import OrderInputModal from "@/components/order/OrderInputModal.vue";
import dayjs from "dayjs";

export default {
  name: "OrderDetail",
  component: {
    dayjs,
  },
  components: { ProductDetailComponent, OrderInputModal },
  data: () => ({
    readonly: false,
    order: null,
    cancelOrderModal: false,
    tempOrderNo: null,
    tempOrderDetail: null,
  }),
  methods: {
    byteSize(str) {
      return new Blob([str]).size;
    },
    sliceByByte(str, maxByte) {
      var b, i, c;
      for (b = i = 0; (c = str.charCodeAt(i)); ) {
        b += c >> 7 ? 2 : 1;

        if (b > maxByte) break;

        i++;
      }
      return str.substring(0, i);
    },
    openCancelOrderModal(orderNo, orderDetail) {
      this.cancelOrderModal = true;
      this.tempOrderNo = orderNo;
      this.tempOrderDetail = orderDetail;
    },
    cancelOrder() {
      this.tempOrderDetail.orderNo = this.tempOrderNo;
      console.log(this.tempOrderDetail);
      api.cancelOrder(this.tempOrderDetail).then((response) => {
        api.getOrderDetail(this.$route.query.orderNo).then((response) => {
          this.$store.state.order.order = response.data;
          this.$store.state.order.order.orderDate = dayjs(
            this.$store.state.order.order.orderDate,
          ).format("YYYY-MM-DD HH:mm:ss");
        });
      });
      this.cancelOrderModal = false;
    },
  },
  created() {
    api.getOrderDetail(this.$route.query.orderNo).then((response) => {
      this.$store.state.order.order = response.data;
      console.log(response.data);
      this.$store.state.order.order.orderDate = dayjs(
        this.$store.state.order.order.orderDate,
      ).format("YYYY-MM-DD HH:mm:ss");
    });
  },
};
</script>

